import React from 'react'
import { useState } from 'react';
import Button from '@seaweb/coral/components/Button';
import Select, { SelectButton, SelectList, SelectOption } from '@seaweb/coral/components/Select';
import { ContentContainer, SegmentContainer, DemoContainer } from '../Containers'

const SelectShowcase = () => {
  const [value, setValue] = useState(1);
  return (
    <ContentContainer>
      <p>Select allows users to make a single selection from a list of options.</p>
      <SegmentContainer>
        <h3>Interactive Demo</h3>
        <DemoContainer>
          <div className="gridcenter">
            <div className="flex-container" style={{padding: "2rem 0"}}>
              <Select value={value} onChange={setValue}>
                <SelectButton placeholder="Select a number" />
                <SelectList>
                  <SelectOption value={0}>Zero</SelectOption>
                  <SelectOption value={1}>One</SelectOption>
                  <SelectOption value={2}>Two</SelectOption>
                  <SelectOption value={3}>Three</SelectOption>
                </SelectList>
              </Select>
              {value !== null && (
                <Button onClick={() => setValue(null)} variant="text">
                  Clear
                </Button>
              )}
            </div>
          </div>
        </DemoContainer>
      </SegmentContainer>

    </ContentContainer>
  )
}

export default SelectShowcase
